无赠品，若非仓储物流引起的问题则认定为乙方商品的质量问题，责任由乙方承担。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          2、乙方应在赠品搭售或赠送活动期间，应准备足够的赠品货源以保证所售商品与客户看到的描述相符。若在活动期间出现类似缺少赠品的问题，由此带来的客户投诉等相关问题，乙方应配合协商处理，并及时给予客户赔偿，甲方不承担任何责任。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          3、乙方在送货时必须将赠品与所对应的商品进行搭固组合且符合甲方的要求，包括但不限于捆扎、收缩膜捆扎、独立组合包装等方式。乙方如要求由甲方代为提供赠品的捆绑服务，则相关费用由乙方承担，具体费用另行确定。如赠品搭售或赠送活动是由甲方发起的，则搭固等捆扎费用由甲方承担。\n        ")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("\n          七、退货及下架\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          1、乙方所提供之商品如依甲方全权判断发生滞销、变质、因乙方原因造成包装不良或损坏、交货规格或数量不符、乙方有违反法律、法规和规章规定或受行政处罚的，或甲、乙双方已终止合同关系的，甲方可自由选择退货、下架，乙方应无条件接受。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          2、商品保质期限剩余时间不足二分之一的货品，甲方可通知乙方进行退货处理。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          3、乙方在收到甲方取货通知后，应在3日内取走相应商品。乙方未按时取货，甲方仓库有权自行发出货物，乙方须认可甲方仓库发货凭证上的商品数量，退货商品在运输过程中的全部费用和风险由乙方承担。乙方退货时选择快递形式退货的，提前与甲方仓库人员电话沟通，并以邮件方式提出申请，以甲方仓库实际签发的数量作为实际退货数量。退货商品在快递过程中的全部费用和风险由乙方承担。\n        ")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("\n          八、货款结算与支付\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          1、结算方式：\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          （1）销售过程中，甲方为乙方代收商品销售款，甲方从乙方商品首次入仓之日起算，每满30天，乙方将前30天的实际售卖订单数据发送至甲方指定邮箱，甲方应在收到邮件后7-15个工作日内核对，核查无误后回传至乙方指定邮箱。甲乙双方确定无误后，甲方在3个工作日内支付费用至乙方指定账户，应付款项=销售款-服务费用-仓储费-其他甲方代付代扣费用（如有）。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          双方如有异议，须在收到邮件后3个工作日内向对方提出异议，双方共同查明原因后协商解决。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          如甲乙双方结算应付款项时出现代收销售款不足抵扣乙方应付的各项费用，甲方可选择直接向乙方追讨费用，也可选择顺延至下一期应付款项中扣除。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          （2）若购买用户要求开具发票的，乙方按照平台销售价格负责开具，并由乙方承担由此产生的税费。。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          （3）当出现用户因质量问题退货等原因造成退款时，如甲乙双方存在未结算款项的，则可从未结算款项中直接扣减；如甲乙双方款项已经结清，乙方需要协助甲方进行退款，将相应的退款汇入甲方公司账户并处理退货等后期处理工作。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          2、支付方式\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          银行转账方式支付。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n